body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  margin: 0;
  padding: 0;
  width: 100vw;
  padding: 32px;
  display: flex;
  min-height: 100vh;
  overflow-x: hidden;
  text-align: center;
  align-items: center;
  box-sizing: border-box;
  justify-content: center;
}

.feature-phone__wrapper {
  padding: 10px;
  border-radius: 32px;
  background: #fe6754;
  box-sizing: border-box;
  border: 4px solid #fe5640;
}

.feature-phone__screen {
  width: 100%;
  height: 220px;
  position: relative;
  border-radius: 24px;
  border-bottom-left-radius: 12px;
  border-bottom-right-radius: 12px;
  font-family: "VT323", sans-serif;
  box-shadow: 0px 0px 1px #141817;
  background: linear-gradient(90deg, #1f1f1f, #999999);
}

.feature-phone__background {
  top: 0;
  left: 0;
  width: inherit;
  height: inherit;
  position: absolute;
  background: #1f1f1f;
  border-top-right-radius: 24px;
  box-shadow: 0px 0px 1px #141817;
  border-bottom-right-radius: 12px;
  -webkit-clip-path: polygon(80% 0, 100% 0, 100% 100%, 60% 100%);
          clip-path: polygon(80% 0, 100% 0, 100% 100%, 60% 100%);
}

.feature-phone__time,
.feature-phone__battery {
  top: 42px;
  z-index: 2;
  font-size: 16px;
  position: absolute;
}
.feature-phone__time {
  right: 10px;
  padding: 0px 4px;
}

.feature-phone__battery {
  margin: 3px 4px;
  width: 20px;
  left: 10px;
  background: #424242;
  height: 10px;
  -webkit-clip-path: polygon(
    30% 0,
    100% 0,
    100% 100%,
    15% 100%,
    15% 70%,
    0 70%,
    0 30%,
    15% 30%,
    15% 0,
    30% 0,
    30% 80%,
    70% 80%,
    70% 20%,
    30% 20%
  );
          clip-path: polygon(
    30% 0,
    100% 0,
    100% 100%,
    15% 100%,
    15% 70%,
    0 70%,
    0 30%,
    15% 30%,
    15% 0,
    30% 0,
    30% 80%,
    70% 80%,
    70% 20%,
    30% 20%
  );
}

.feature-phone__display {
  bottom: 8px;
  width: 100%;
  margin: 10px;
  height: 160px;
  display: flex;
  overflow: hidden;
  position: absolute;
  align-items: center;
  background: #ffffff;
  flex-direction: column;
  -webkit-filter: brightness(80%);
          filter: brightness(80%);
  width: calc(100% - 20px);
  justify-content: flex-end;
  box-shadow: inset 1px 1px 2px rgba(0, 0, 0, 0.6),
    inset -1px -1px 2px rgba(0, 0, 0, 0.4);
}
.feature-phone__message {
  padding: 4px;
  font-size: 18px;
  font-weight: 400;
}

.feature-phone__help {
  width: 100%;
  display: flex;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  justify-content: space-between;
  background: rgba(0, 0, 0, 0.5);
}
.feature-phone__help-left,
.feature-phone__help-right {
  font-size: 16px;
  padding: 4px 8px;
  color: #ffffff;
}

.feature-phone__grill {
  top: 12px;
  left: 50%;
  height: 4px;
  width: 50px;
  border-radius: 2px;
  position: absolute;
  -webkit-filter: blur(0.1px);
          filter: blur(0.1px);
  background: #262d2c;
  box-shadow: 0.5px 0.5px 0.5px #707070, inset 0.5px 0.5px 0.5px #000000;
  transform: translateX(-50%);
}

.feature-phone__name {
  top: 24px;
  left: 50%;
  width: 64px;
  height: auto;
  height: 4px;
  color: #ffffff;
  font-size: 14px;
  font-weight: bold;
  padding-left: 4px;
  position: absolute;
  border-radius: 2px;
  letter-spacing: 2px;
  font-family: monospace;
  transform: translateX(-50%);
}

.feature-phone__buttons-wrapper {
  margin-top: 12px;
}

.feature-phone__dummy-buttons,
.feature-phone__buttons {
  display: flex;
  align-items: center;
  justify-content: center;
}

.feature-phone__buttons {
  width: 168px;
  margin: -2px;
  flex-wrap: wrap;
}

.feature-phone__mic-wrapper {
  height: 12px;
  display: flex;
  padding: 4px 0px;
  align-items: flex-end;
  justify-content: center;
}
.feature-phone__mic {
  width: 4px;
  height: 3px;
  border-radius: 3px;
  background: linear-gradient(145deg, #262d2c, #2d3634);
  box-shadow: 0px 0px 1px #141817, inset 0px 0px 0.5px #000000;
}

.keypad__key {
  margin: 2px;
  width: 52px;
  height: 24px;
  display: flex;
  cursor: pointer;
  color: #cccccc;
  font-size: 12px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  border-radius: 16px;
  align-items: center;
  justify-content: center;
  box-shadow: 0px 0px 1px #141817;
  will-change: transform, box-shadow;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  background: linear-gradient(145deg, #36413e, #262d2c);
}

.keypad__key.keypad__key--center {
  height: 52px;
  border-radius: 20px;
  position: relative;
  pointer-events: none;
}

.keypad__key-large {
  width: 38px;
  height: 38px;
  position: absolute;
  border-radius: 14px;
  box-shadow: inset 1px 1px 3px #141414,inset -1px -1px 3px #141414;
  background: #2d3634;
}

.keypad__key:hover,
.keypad__key:focus {
  outline: none;
  box-shadow: none;
  color: #c2c2c2;
  background: linear-gradient(145deg, #262d2c, #36413e);
}

.keypad__key-value {
  width: 12px;
  font-size: 14px;
}
.keypad__key-binding {
  width: 24px;
  font-size: 10px;
}

.call-animation {
  padding-bottom: 8px;
}

.call-animation__img {
  width: 52px;
  animation: call 0.3s linear infinite alternate-reverse;
}

.call-animation__text:after {
  content: " .";
  -webkit-animation: dots 1s steps(5, end) infinite;
          animation: dots 1s steps(5, end) infinite;
}

@-webkit-keyframes dots {
  0%,
  20% {
    color: rgba(0, 0, 0, 0);
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  40% {
    color: black;
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  60% {
    text-shadow: 0.25em 0 0 black, 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  80%,
  100% {
    text-shadow: 0.25em 0 0 black, 0.5em 0 0 black;
  }
}

@keyframes dots {
  0%,
  20% {
    color: rgba(0, 0, 0, 0);
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  40% {
    color: black;
    text-shadow: 0.25em 0 0 rgba(0, 0, 0, 0), 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  60% {
    text-shadow: 0.25em 0 0 black, 0.5em 0 0 rgba(0, 0, 0, 0);
  }
  80%,
  100% {
    text-shadow: 0.25em 0 0 black, 0.5em 0 0 black;
  }
}

@-webkit-keyframes call {
  0%,
  80% {
    transform: rotate(5deg);
  }
  20%,
  100% {
    transform: rotate(-5deg);
  }
}

@keyframes call {
  0%,
  80% {
    transform: rotate(5deg);
  }
  20%,
  100% {
    transform: rotate(-5deg);
  }
}

input {
  border: none;
  outline: none;
  width: inherit;
  font-size: 24px;
  text-align: right;
  background: #ffffff;
  font-family: "VT323", sans-serif;
}

.send-btn .asterix {
  font-size: 11px;
  margin-bottom: 2px;
}

.send-btn .keypad__key-binding {
  padding-top: 2px;
}

